<script>
import { computed, ref, watch } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();

        const monitorLines = computed(() => store.state.cycleMonitoring.lines);
        const selectedMonitorLines = ref();

        watch(selectedMonitorLines, (newValue) => {
            store.commit("cycleMonitoring/SET_SELECTED_MONITOR_LINE", newValue);
        });

        return { monitorLines, selectedMonitorLines };
    }
};
</script>
