<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-formgrid p-grid">
                <div class="p-field p-col-12">
                    <label for="other-drugs" class="p-mb-1">
                        Other Drugs
                    </label>
                    <p-input-text
                        v-model="form.otherDrugLine1"
                        id="other-drugs"
                        type="text"
                    />
                </div>
                <div class="p-field p-col-12">
                    <label for="other-drug-line2" class="p-mb-1">
                        Patient Comments
                    </label>
                    <p-input-text
                        v-model="form.otherDrugLine2"
                        id="other-drug-line2"
                        type="text"
                    />
                </div>
                <small
                    v-if="
                        (v$.otherDrugLine2.$invalid && submitted) ||
                            v$.otherDrugLine2.$pending.$response
                    "
                    class="p-error"
                    >{{ v$.otherDrugLine2.required.$message }}
                </small>
            </div>
        </template>
    </p-card>
</template>

<script>
import {
    computed,
    inject,
    reactive,
    ref,
    watch,
    onMounted,
    onUnmounted
} from "vue";
import { useStore } from "vuex";
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );

        const summaryForm = inject("summaryForm");
        const form = reactive({
            otherDrugLine1: null,
            otherDrugLine2: null
        });

        watch(form, (newValue) => {
            summaryForm.otherDrugLine1 = newValue.otherDrugLine1;
            summaryForm.otherDrugLine2 = newValue.otherDrugLine2;
        });

        const rules = {
            otherDrugLine2: { required }
        };
        const v$ = useVuelidate(rules, form);
        const submitted = ref(false);
        const isValid = inject("isValid");
        const onSave = () => {
            submitted.value = true;
            isValid.otherDrugForm = !v$.value.$invalid;
        };

        onMounted(() => {
            emitter.on("cycle-monitoring-toolbar:save", onSave);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return { form, v$, submitted };
    }
};
</script>
