<template>
    <p-toolbar class="p-p-2">
        <template #left>
            <p-button
                label="Edit Day"
                class="p-mr-2 p-button-success"
                @click="editDay"
                :disabled="!selectedMonitorLine"
            />
            <p-button
                label="Previous Cycle"
                icon="pi pi-chevron-left"
                class="p-mr-2"
                @click="previousCycle"
            />
            <p-button
                label="Next Cycle"
                icon="pi pi-chevron-right"
                iconPos="right"
                @click="nextCycle"
            />
        </template>
    </p-toolbar>
</template>

<script>
import { computed } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const selectedMonitorLine = computed(
            () => store.state.cycleMonitoring.selectedMonitorLine
        );

        return {
            selectedMonitorLine,
            editDay: () => {
                emitter.emit("cycle-monitoring-cycle-nav:edit-day", "edit");
            },
            nextCycle: () => store.dispatch("artCycle/nextCycle"),
            previousCycle: () => store.dispatch("artCycle/previsouCycle")
        };
    }
};
</script>

<style scoped>
.p-toolbar {
    background-color: transparent;
    border: none;
}
</style>
