<template>
    <p-toolbar>
        <template #left>
            <!-- <p-button
                label="Edit Day"
                class="p-mr-2 p-button-success"
                @click="editDay"
                :disabled="!selectedMonitorLine"
            /> -->
            <p-button
                label="Save Comment"
                class="p-mr-2 p-button-info"
                @click="saveComment"
                :disabled="!selectedComment"
            />
            <p-button label="Next" class="p-mr-2 p-button-secondary" />
            <p-button
                label="New Day"
                class="p-mr-2 p-button-primary"
                @click="newDay"
            />
            <p-button label="Delete Day" class="p-mr-2 p-button-danger" />
            <p-button label="Copy Day" class="p-mr-2 p-button-secondary" />
            <p-button label="Deletion Day" class="p-mr-2 p-button-danger" />
            <p-button label="PDF Report" class="p-mr-2 p-button-secondary" />
            <p-button
                label="Order Blood Test"
                class="p-mr-2 p-button-secondary"
            />
        </template>
    </p-toolbar>
</template>

<script>
import { computed, ref, watch, watchEffect } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const selectedMonitorLine = computed(
            () => store.state.cycleMonitoring.selectedMonitorLine
        );
        const selectedComment = computed(
            () => store.state.cycleMonitoring.selectedComment
        );
        const disabled = ref(true);

        const saveComment = () => {
            store.dispatch("cycleMonitoring/updateComment").then(() => {
                emitter.emit(
                    "cycle-monitoring:reload",
                    store.state.artCycle.selectedCycle.cycleId
                );
            });
        };

        return {
            disabled,
            selectedMonitorLine,
            selectedComment,
            // editDay: () => {
            //     emitter.emit("cycle-monitoring-day-toolbar:edit-day", "edit");
            // },
            newDay: () => {
                emitter.emit("cycle-monitoring-day-toolbar:new-day", "new");
            },
            saveComment
        };
    }
};
</script>
