import { format } from "date-fns";
import { reactive } from "vue";

export function useCycleMonitoringForm(store) {
    const form = reactive({
        patientId: store.state.artCycle.patient
            ? store.state.artCycle.patient.id
            : null,
        cycleId: store.state.artCycle.selectedCycle
            ? store.state.artCycle.selectedCycle.cycleId
            : null,
        type: null,
        no: null,
        cancelled: false,
        funded: false,
        date: null,
        diagnosis1: null,
        diagnosis2: null,
        diagnosis3: null,
        lmpDate: null,
        lengthInDays: null,
        drug1: null,
        lutealSupport: false,
        drug2: null,
        totalDose: null,
        simulation: null,
        protocol: null,
        protocolText: null,
        hcgiu: false,
        intralipid: false,
        pgs: false,
        pgd: false,
        tese: false,
        fragmin: false,
        neurogen: false,
        caLanophore: false,
        ah: false,
        labAware: false,
        sbwf: null,
        sbwm: null,
        sono: null,
        labComment: null,
        covered: null,
        fees: null,
        nextPlan: null,
        consents: null,
        otherDrugLine1: null,
        otherDrugLine2: null
    });

    const isValid = reactive({
        cycleDetailForm: false,
        therapyForm: false,
        cycleInfoForm: false,
        otherDrugForm: false
    });

    return { form, isValid };
}

