export function useFormatToFromData(details, line, left, right) {
    return {
        cycleId: details.cycleId, //required
        monId: details.monitorId, //required
        curPatientId: details.patientId, //required
        isSigned: true, //required
        nmlDate: format(line.date, "yyyy-MM-dd"),
        nmlCDay: line.cycleDay,
        nmlIDay: line.injecttionDay,
        nmlRTot: line.rightOvaryTotal,
        nmlLTot: line.leftOvaryTotal,
        nmlR10ls: right.input10l,
        nmlR11: right.input11,
        nmlR12: right.input12,
        nmlR13: right.input13,
        nmlR14: right.input14,
        nmlR15: right.input15,
        nmlR16: right.input16,
        nmlR17: right.input17,
        nmlR18: right.input18,
        nmlR19: right.input19,
        nmlR20: right.input20,
        nmlR21: right.input21,
        nmlR22: right.input22,
        nmlR23: right.input23,
        nmlR24gt: right.input24g,
        nmlL10ls: left.input10l,
        nmlL11: left.input11,
        nmlL12: left.input12,
        nmlL13: left.input13,
        nmlL14: left.input14,
        nmlL15: left.input15,
        nmlL16: left.input16,
        nmlL17: left.input17,
        nmlL18: left.input18,
        nmlL19: left.input19,
        nmlL20: left.input20,
        nmlL21: left.input21,
        nmlL22: left.input22,
        nmlL23: left.input23,
        nmlL24gt: left.input24g,
        nmlET: line.et,
        nmlGnT: line.gnt,
        nmlRCyst: line.rightOvaryCyst,
        nmlLCyst: line.leftOvaryTotal,
        nmlPolyp: line.polyp,
        nmlComment: line.comments
    };
}

export function useOnShow({ type, form, selectedLine, ovaryFollicles, store }) {
    return () => {
        const right = ovaryFollicles[0];
        const left = ovaryFollicles[1];

        if (type.value === "edit") {
            form.cmLineId = selectedLine.value.cm_lines_id;
            form.date = new Date(selectedLine.value.cycleDate);
            form.cycleId = store.state.cycleMonitoring.details.cycleId;
            form.monitorId = store.state.cycleMonitoring.details.monitorId;
            form.cycleDay = parseInt(selectedLine.value.cycleDay);
            form.injecttionDay = parseInt(selectedLine.value.injectionDay);
            form.rightOvaryTotal = parseInt(selectedLine.value.rTotal);
            form.rightOvaryCyst = selectedLine.value.rCyst;
            form.leftOvaryTotal = parseInt(selectedLine.value.lTotal);
            form.leftOvaryCyst = selectedLine.value.lCyst;
            form.et = Number(selectedLine.value.etUs);
            form.polyp = selectedLine.value.polyp;
            form.gnt = selectedLine.value.gnt;

            right.input10l = selectedLine.value.or10ls;
            right.input11 = selectedLine.value.or11;
            right.input12 = selectedLine.value.or12;
            right.input13 = selectedLine.value.or13;
            right.input14 = selectedLine.value.or14;
            right.input15 = selectedLine.value.or15;
            right.input16 = selectedLine.value.or16;
            right.input17 = selectedLine.value.or17;
            right.input18 = selectedLine.value.or18;
            right.input19 = selectedLine.value.or19;
            right.input20 = selectedLine.value.or20;
            right.input21 = selectedLine.value.or12;
            right.input22 = selectedLine.value.or22;
            right.input23 = selectedLine.value.or23;
            right.input24g = selectedLine.value.or24gt;

            left.input10l = selectedLine.value.ol10ls;
            left.input11 = selectedLine.value.ol11;
            left.input12 = selectedLine.value.ol12;
            left.input13 = selectedLine.value.ol13;
            left.input14 = selectedLine.value.ol14;
            left.input15 = selectedLine.value.ol15;
            left.input16 = selectedLine.value.ol16;
            left.input17 = selectedLine.value.ol17;
            left.input18 = selectedLine.value.ol18;
            left.input19 = selectedLine.value.ol19;
            left.input20 = selectedLine.value.ol20;
            left.input21 = selectedLine.value.ol12;
            left.input22 = selectedLine.value.ol22;
            left.input23 = selectedLine.value.ol23;
            left.input24g = selectedLine.value.ol24gt;
        }

        if (type.value === "new") {
            form.date = null;
            form.cycleId = store.state.artCycle.selectedCycle.cycleId;
            form.cycleDay = null;
            form.injecttionDay = null;
            form.rightOvaryTotal = null;
            form.rightOvaryCyst = null;
            form.leftOvaryTotal = null;
            form.leftOvaryCyst = null;
            form.et = null;
            form.polyp = null;
            form.gnt = null;
            right.input10l = null;
            right.input11 = null;
            right.input12 = null;
            right.input13 = null;
            right.input14 = null;
            right.input15 = null;
            right.input16 = null;
            right.input17 = null;
            right.input18 = null;
            right.input19 = null;
            right.input20 = null;
            right.input21 = null;
            right.input22 = null;
            right.input23 = null;
            right.input24g = null;
            left.input10l = null;
            left.input11 = null;
            left.input12 = null;
            left.input13 = null;
            left.input14 = null;
            left.input15 = null;
            left.input16 = null;
            left.input17 = null;
            left.input18 = null;
            left.input19 = null;
            left.input20 = null;
            left.input21 = null;
            left.input22 = null;
            left.input23 = null;
            left.input24g = null;
        }
    };
}

export function useFormatToMonitorSummaryReqBody({ data, store }) {
    return {
        monitorId: store.state.cycleMonitoring.details.monitorId,
        cycleId: data.cycleId,
        cycleType: data.type,
        cycleNo: data.no,
        lmpDate: data.lmpDate ? format(data.lmpDate, "yyyy-MM-dd") : null,
        isCancelled: data.cancelled,
        lutSupport: data.lutealSupport,
        luteaDrug: data.drug2,
        cancelDate: null,
        stimProt: data.simulation,
        stimProt2: data.protocol,
        stimProt2Text: data.protocolText,
        diag1: data.diagnosis1,
        diag2: data.diagnosis2,
        diag3: data.diagnosis3,
        patientComment: null,
        agonDrug: data.drug1,
        hcgiu: data.hcgiu,
        intralipid: data.intralipid,
        fragmin: data.fragmin,
        neurogen: data.neurogen,
        cover: !!data.covered,
        fees: data.fees,
        lengthInDays: data.lengthInDays,
        nextPlan: data.nextPlan,
        consents: data.consents,
        totalDose: data.totalDose,
        labComment: data.labComment,
        sono: data.sono,
        sbwm: data.sbwm,
        sbwf: data.sono,
        labAware: data.labAware,
        alertMsg: null,
        curPatientId: data.patientId,
        otherDrugLine1: data.otherDrugLine1,
        patientComment: data.otherDrugLine2
    };
}
