<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-12">
                    <div class="p-field p-grid">
                        <label for="diagnosis1" class="p-col-fixed p-mb-0">
                            Diagnosis 1
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                id="diagnosis1"
                                v-model="form.diagnosis1"
                                :options="diagnosisOptions"
                                optionLabel="label"
                                optionValue="value"
                                :filter="true"
                                :editable="true"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-12">
                    <div class="p-field p-grid">
                        <label for="diagnosis2" class="p-col-fixed p-mb-0">
                            Diagnosis 3
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                id="diagnosis2"
                                v-model="form.diagnosis2"
                                :options="diagnosisOptions"
                                optionLabel="label"
                                optionValue="value"
                                :filter="true"
                                :editable="true"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-12">
                    <div class="p-field p-grid">
                        <label for="diagnosis3" class="p-col-fixed p-mb-0">
                            Diagnosis 3
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                id="diagnosis3"
                                v-model="form.diagnosis3"
                                :options="diagnosisOptions"
                                optionLabel="label"
                                optionValue="value"
                                :filter="true"
                                :editable="true"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { reactive, computed, watch, onMounted, onUnmounted, inject } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );

        const diagnosisOptions = computed(
            () => store.state.cycleMonitoring.diagnosisOptions
        );

        const summaryForm = inject("summaryForm");
        const form = reactive({
            diagnosis1: null,
            diagnosis2: null,
            diagnosis3: null
        });
        watch(form, newValue => {
            summaryForm.diagnosis1 = newValue.diagnosis1
            summaryForm.diagnosis2 = newValue.diagnosis2
            summaryForm.diagnosis3 = newValue.diagnosis3
        })

        watch(monitorDetails, (newValue) => {
            form.diagnosis1 = newValue.diag1;
            form.diagnosis2 = newValue.diag2;
            form.diagnosis3 = newValue.diag3;
        });

        const onSave = () => {
            // console.log("from diagnosis ", { ...form });
        };

        onMounted(() => {
            emitter.on("cycle-monitoring-toolbar:save", onSave);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return { diagnosisOptions, form };
    }
};
</script>
