<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-12 p-md-6">
                    <div class="p-field p-grid">
                        <label for="cycle-type" class="p-col-fixed p-mb-0">
                            Cycle Type
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="cycle.type"
                                id="cycle-type"
                                type="text"
                                :disabled="disable"
                                :class="{
                                    'p-invalid': submitted && v$.type.$invalid
                                }"
                            />
                        </div>
                    </div>
                    <small
                        v-if="
                            (v$.type.$invalid && submitted) ||
                                v$.type.$pending.$response
                        "
                        class="p-error"
                        >{{
                            v$.type.required.$message.replace(
                                "Value",
                                "Cycle Type"
                            )
                        }}
                    </small>
                </div>
                <div class="p-col-12 p-md-6">
                    <div class="p-field p-grid">
                        <label for="cycle-no" class="p-col-fixed p-mb-0">
                            Cycle #
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="cycle.no"
                                id="cycle-no"
                                type="text"
                                :disabled="disable"
                                style="width: 40px"
                                :class="{
                                    'p-invalid': submitted && v$.no.$invalid
                                }"
                            />
                        </div>
                    </div>
                    <small
                        v-if="
                            (v$.no.$invalid && submitted) ||
                                v$.no.$pending.$response
                        "
                        class="p-error"
                        >{{
                            v$.no.required.$message.replace(
                                "Value",
                                "Cycle No"
                            )
                        }}
                    </small>
                </div>
                <div class="p-col-12">
                    <div class="p-field p-grid">
                        <label
                            for="cycle-start-date"
                            class="p-col-fixed p-mb-0"
                        >
                            Cycle Start Date
                        </label>
                        <div class="p-col">
                            <p-calendar
                                id="cycle-start-date"
                                :showIcon="true"
                                v-model="cycle.date"
                                :disabled="disable"
                                dateFormat="yy.mm.dd"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-12 p-md-6">
                    <div class="p-field p-grid">
                        <div class="p-col-fixed">
                            <p-toggle-button
                                v-model="cycle.cancelled"
                                onIcon="pi pi-check"
                                offIcon="pi pi-times"
                                :disabled="disable"
                            />
                        </div>
                        <label for="cycle-cancelled" class="p-col p-mb-0">
                            Cycle Canceled
                        </label>
                    </div>
                </div>
                <div class="p-col-12 p-md-6">
                    <div class="p-field p-grid">
                        <div class="p-col-fixed">
                            <p-toggle-button
                                v-model="cycle.funded"
                                onIcon="pi pi-check"
                                offIcon="pi pi-times"
                                :disabled="disable"
                            />
                        </div>
                        <label for="funded" class="p-col p-mb-0">
                            Funded
                        </label>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import {
    onMounted,
    reactive,
    ref,
    watch,
    computed,
    inject,
    onUnmounted
} from "vue";
import { useStore } from "vuex";
import { required } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const selectedCycle = computed(
            () => store.state.artCycle.selectedCycle
        );

        const summaryForm = inject("summaryForm");
        const cycle = reactive({
            type: null,
            no: null,
            cancelled: false,
            funded: false,
            date: null
        });
        const rules = {
            type: { required },
            no: { required }
        };
        const v$ = useVuelidate(rules, cycle);
        const submitted = ref(false);
        const isValid = inject("isValid");
        const onSave = () => {
            submitted.value = true;
            isValid.cycleInfoForm = !v$.value.$invalid;
        };

        watch(cycle, (newValue) => {
            summaryForm.type = newValue.type;
            summaryForm.no = newValue.no;
            summaryForm.cancelled = newValue.cancelled;
            summaryForm.funded = newValue.funded;
            summaryForm.date = newValue.date;
        });

        const disable = ref(false);

        const fillCycleInfo = () => {
            let _cycle = store.state.artCycle.selectedCycle;

            if (_cycle) {
                cycle.type = _cycle.cycleType;
                cycle.no = _cycle.cycleNo;
                cycle.cancelled = _cycle.cancel;
                cycle.date = _cycle.cycleDate.split(" ")[0];
                disable.value = true;
            }
        };

        watch(selectedCycle, (newValue) => {
            fillCycleInfo();
        });

        onMounted(() => {
            fillCycleInfo();
            emitter.on("cycle-monitoring-toolbar:save", onSave);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return {
            cycle,
            disable,
            submitted,
            v$
        };
    }
};
</script>
