<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-12 p-formgroup-inline">
                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.hcgiu" :binary="true" />
                        <label for="city1">HCGIU</label>
                    </div>
                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.intralipid" :binary="true" />
                        <label for="city2">Intralipid</label>
                    </div>
                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.pgs" :binary="true" />
                        <label for="city3">PGS</label>
                    </div>
                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.pgd" :binary="true" />
                        <label for="city3">PGD</label>
                    </div>
                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.tese" :binary="true" />
                        <label for="city4">TESE</label>
                    </div>
                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.fragmin" :binary="true" />
                        <label for="city1">Fragmin</label>
                    </div>
                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.neurogen" :binary="true" />
                        <label for="city2">Neurogen</label>
                    </div>
                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.caLanophore" :binary="true" />
                        <label for="city3">Ca lanophore</label>
                    </div>
                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.ah" :binary="true" />
                        <label for="city4">AH</label>
                    </div>
                    <div class="p-field-checkbox">
                        <p-checkbox v-model="form.labAware" :binary="true" />
                        <label for="city4">Lab Aware</label>
                    </div>
                </div>
            </div>

            <div class="p-fluid p-grid">
                <div class="p-field p-col-12 p-md-2">
                    <div class="p-field p-grid">
                        <label for="sbw-f" class="p-col-fixed p-mb-0">
                            SBW F
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.sbwf"
                                id="sbw-f"
                                type="text"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-field p-col-12 p-md-2">
                    <div class="p-field p-grid">
                        <label for="sbw-m" class="p-col-fixed p-mb-0">
                            SBW M
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.sbwm"
                                id="sbw-m"
                                type="text"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-field p-col-12 p-md-2">
                    <div class="p-field p-grid">
                        <label for="sono" class="p-col-fixed p-mb-0">
                            SONO
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.sono"
                                id="sono"
                                type="text"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-field p-col-12 p-md-6">
                    <div class="p-field p-grid">
                        <label for="lab-comment" class="p-col-fixed p-mb-0">
                            Lab Comment
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.labComment"
                                id="lab-comment"
                                type="text"
                                :class="{
                                    'p-invalid':
                                        submitted && v$.labComment.$invalid
                                }"
                            />
                        </div>
                    </div>
                    <small
                        v-if="
                            (v$.labComment.$invalid && submitted) ||
                                v$.labComment.$pending.$response
                        "
                        class="p-error"
                        >{{
                            v$.labComment.required.$message.replace(
                                "Value",
                                "Lab Comment"
                            )
                        }}
                    </small>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import {
    computed,
    inject,
    ref,
    reactive,
    watch,
    onMounted,
    onUnmounted
} from "vue";
import { useStore } from "vuex";
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );
        const summaryForm = inject("summaryForm");
        const form = reactive({
            hcgiu: false,
            intralipid: false,
            pgs: false,
            pgd: false,
            tese: false,
            fragmin: false,
            neurogen: false,
            caLanophore: false,
            ah: false,
            labAware: false,
            sbwf: null,
            sbwm: null,
            sono: null,
            labComment: null
        });

        watch(form, (newValue) => {
            summaryForm.hcgiu = newValue.hcgiu;
            summaryForm.intralipid = newValue.intralipid;
            summaryForm.pgs = newValue.pgs;
            summaryForm.pgd = newValue.pgd;
            summaryForm.tese = newValue.tese;
            summaryForm.fragmin = newValue.fragmin;
            summaryForm.neurogen = newValue.neurogen;
            summaryForm.caLanophore = newValue.caLanophore;
            summaryForm.ah = newValue.ah;
            summaryForm.labAware = newValue.labAware;
            summaryForm.sbwf = newValue.sbwf;
            summaryForm.sbwm = newValue.sbwm;
            summaryForm.sono = newValue.sono;
            summaryForm.labComment = newValue.labComment;
        });

        watch(monitorDetails, (newValue) => {
            form.hcgiu = !!newValue.hcgiu;
            form.intralipid = !!newValue.intralipid;
            form.fragmin = !!newValue.fragmin;
            form.neurogen = !!newValue.neurogen;
            form.labAware = !!newValue.labAware;
            form.sbwf = newValue.sbwf;
            form.sbwm = newValue.sbwm;
            form.sono = newValue.sono;
            form.labComment = newValue.labComment;
            submitted.value = false;
        });

        const rules = {
            labComment: { required }
        };
        const v$ = useVuelidate(rules, form);
        const submitted = ref(false);
        const isValid = inject("isValid");
        const onSave = () => {
            submitted.value = true;
            isValid.therapyForm = !v$.value.$invalid;
        };

        onMounted(() => {
            emitter.on("cycle-monitoring-toolbar:save", onSave);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return { form, v$, submitted };
    }
};
</script>
