<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-12 p-md-4">
                    <div class="p-field p-grid">
                        <label for="lmp-date" class="p-col-fixed p-mb-0">
                            LMP Date
                        </label>
                        <div class="p-col">
                            <p-calendar
                                id="lmp-date"
                                :showIcon="true"
                                v-model="form.lmpDate"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-12 p-md-4">
                    <div class="p-field p-grid">
                        <label for="length-in-days" class="p-col-fixed p-mb-0">
                            Length in Days
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.lengthInDays"
                                id="length-in-days"
                                style="width: 40px"
                                :class="{
                                    'p-invalid':
                                        submitted && v$.lengthInDays.$invalid
                                }"
                            />
                        </div>
                    </div>
                    <small
                        v-if="
                            (v$.lengthInDays.$invalid && submitted) ||
                                v$.lengthInDays.$pending.$response
                        "
                        class="p-error"
                        >{{
                            v$.lengthInDays.required.$message.replace(
                                "Value",
                                "Length In Days"
                            )
                        }}
                    </small>
                </div>
                <div class="p-col-12 p-md-4">
                    <div class="p-field p-grid">
                        <label for="drug1" class="p-col-fixed p-mb-0">
                            Drug
                        </label>

                        <div class="p-col">
                            <!-- agoinst -->
                            <p-dropdown
                                id="drug1"
                                v-model="form.drug1"
                                :options="drug1Options"
                                :filter="true"
                                optionLabel="label"
                                optionValue="value"
                                :editable="true"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-12 p-md-4">
                    <div class="p-field p-grid">
                        <div class="p-col-fixed">
                            <!-- letSuppurt -->
                            <p-checkbox
                                v-model="form.lutealSupport"
                                :binary="true"
                            />
                        </div>
                        <label for="luteal-support" class="p-col p-mb-0">
                            Luteal Support
                        </label>
                    </div>
                </div>
                <div class="p-col-4">
                    <div class="p-field p-grid">
                        <label for="drug2" class="p-col-fixed p-mb-0">
                            Drug
                        </label>
                        <div class="p-col">
                            <!-- luteaDrug -->
                            <p-dropdown
                                id="drug2"
                                v-model="form.drug2"
                                :options="drug2Options"
                                :filter="true"
                                optionLabel="label"
                                optionValue="value"
                                :editable="true"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-4">
                    <div class="p-field p-grid">
                        <label for="total-dose" class="p-col-fixed p-mb-0">
                            Total Dose
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.totalDose"
                                id="total-dose"
                                style="width: 40px"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-12 p-md-4">
                    <div class="p-field p-grid">
                        <label for="simulaiton" class="p-col-fixed p-mb-0">
                            Simulation
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.simulaiton"
                                id="simulaiton"
                                type="text"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-4">
                    <div class="p-field p-grid">
                        <label for="protocol" class="p-col-fixed p-mb-0">
                            Protocol
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                id="protocol"
                                v-model="form.protocol"
                                :options="protocolOptions"
                                :filter="true"
                                optionLabel="label"
                                optionValue="value"
                                :editable="true"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-field p-col-12 p-md-4">
                    <p-input-text v-model="form.protocolText" type="text" />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import {
    computed,
    inject,
    onMounted,
    onUnmounted,
    reactive,
    ref,
    watch
} from "vue";
import { useStore } from "vuex";
import { required } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );
        const protocolOptions = computed(
            () => store.state.cycleMonitoring.protocolOptions
        );
        const drug1Options = computed(
            () => store.state.cycleMonitoring.drugs1Options
        );
        const drug2Options = computed(
            () => store.state.cycleMonitoring.drugs2Options
        );

        const summaryForm = inject("summaryForm");
        const form = reactive({
            lmpDate: null,
            lengthInDays: null,
            drug1: null,
            lutealSupport: false,
            drug2: null,
            totalDose: null,
            simulation: null,
            protocol: null,
            protocolText: null
        });
        const submitted = ref(false);

        watch(form, (newValue) => {
            summaryForm.lmpDate = newValue.lmpDate;
            summaryForm.lengthInDays = newValue.lengthInDays;
            summaryForm.drug1 = newValue.drug1;
            summaryForm.lutealSupport = newValue.lutealSupport;
            summaryForm.drug2 = newValue.drug2;
            summaryForm.totalDose = newValue.totalDose;
            summaryForm.simulation = newValue.simulation;
            summaryForm.protocol = newValue.protocol;
            summaryForm.protocolText = newValue.protocolText;
        });

        watch(monitorDetails, (newValue) => {
            form.lmpDate = newValue.lmpDate;
            form.lengthInDays = newValue.lengthInDays;
            form.drug1 = newValue.agonistDrug;
            form.drug2 = newValue.luteaDrug;
            form.totalDose = parseInt(newValue.totalDose);
            form.lutealSupport = !!newValue.letSupport;
            form.protocolText = newValue.protocolText;
            submitted.value = false;
        });

        const rules = {
            lengthInDays: { required }
        };

        const v$ = useVuelidate(rules, form);
        const isValid = inject("isValid");
        const onSave = () => {
            submitted.value = true;
            isValid.cycleDetailForm = !v$.value.$invalid;
        };

        onMounted(() => {
            emitter.on("cycle-monitoring-toolbar:save", onSave);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return {
            protocolOptions,
            drug1Options,
            drug2Options,
            form,
            v$,
            submitted
        };
    }
};
</script>
